import {Input, Segment, Grid, Icon, Header} from 'semantic-ui-react';
import React, { Component } from 'react';
import moment from 'moment';


export default class DeviceGridColumn extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Grid.Column>
                  <Segment.Group piled horizontal>
                        <Segment.inline>
                        <Header as='h1'>{this.props.name}</Header>
                        {this.props.file ?
                                <Icon color='green' name='check'/>
                            :
                                <Icon color='red' name='close'/>
                        }
                        </Segment.inline>
                    </Segment.Group>
            </Grid.Column>
        )
    }
}

